import { getJSErrorList } from "@/api/modules/web/jserror";
import { Table } from "antd";
import { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";

interface DataType {
    key: React.Key;
    content: string;
    time: string;
    errorCount: number;
    userCount: number;
}

type JSErrorListProps = {
    onClick: (e: any) => void;
};

const JSErrorList: React.FC<JSErrorListProps> = (props: JSErrorListProps) => {
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [errorTotal, setErrorTotal] = useState(0);
    const [errorList, setErrorList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);

    const columns: ColumnsType<DataType> = [
        {
            title: '错误内容',
            dataIndex: 'content',
            width: 200,
            render: (text) => <div><span onClick={props.onClick} style={{ width: '200px', wordBreak: 'break-all' }}>{text}</span></div>,
        },
        {
            title: '起止时间',
            dataIndex: 'time',
            width: 200,
        },
        {
            title: '错误数',
            dataIndex: 'errorCount',
            width: 50,
        },
        {
            title: '影响用户数',
            dataIndex: 'userCount',
            width: 50,
        },
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initJSErrorList = async () => {
        const result = await getJSErrorList({ pageNum, pageSize });
        if (result.data) {
            setErrorTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.errorId,
                    content: item.stack,
                    time: '2020/12/09',
                    errorCount: 5,
                    userCount: 6,
                })
            });
            setErrorList(data);
        }
    };

    useEffect(() => {
        initJSErrorList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initJSErrorList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: errorTotal
    }

    return (<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={errorList} />);
}

export default JSErrorList;