import { Table } from "antd";
import { ColumnsType } from "antd/es/table";
import { useState } from "react";

interface DataType {
    key: React.Key;
    pid: string;
    errorCount: number;
    userCount: number;
}

const PageList: React.FC = (props:any) => {
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);

    const columns: ColumnsType<DataType> = [
        {
            title: 'PID',
            dataIndex: 'pid',
        },
        {
            title: '错误数',
            dataIndex: 'errorCount',
        },
        {
            title: '影响用户数',
            dataIndex: 'userCount',
        },
    ];

    const data: DataType[] = [];
    for (let i = 0; i < 10; i++) {
        data.push({
            key: i,
            pid: 'script error',
            errorCount: 5,
            userCount: 6,
        })
    }

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        console.log('selectedRowKeys changed: ', newSelectedRowKeys);
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    return (<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={false} columns={columns} dataSource={data} />);
}

export default PageList;