import FilterHeader from "@/components/FilterHeader";
import { Card, Drawer, Radio, RadioChangeEvent, Space } from "antd";
import { useState } from "react";
import JSErrorList from "./jsErrorList";
import PageList from "./pageList";
import ErrorInfoList from "./errorInfoList";
import { CloseOutlined } from "@ant-design/icons";

enum ErrorCategory {
    JSErrorList,
    PageList,
    ErrorInfoList
}

const View: React.FC = () => {
    const [category, setCategory] = useState<ErrorCategory>(ErrorCategory.JSErrorList);
    const [open, setOpen] = useState(false);
    const changeCategory = (e: RadioChangeEvent) => {
        setCategory(e.target.value);
    }

    const onClose = () => {
        setOpen(false);
    };

    const showDrawer = () => {
        setOpen(true);
      };

    return (<div>
        <FilterHeader></FilterHeader>
        <Card title="错误及页面列表" style={{ marginTop: '16px' }}>
            <Radio.Group defaultValue={ErrorCategory.JSErrorList} buttonStyle="solid" onChange={changeCategory}>
                <Radio.Button value={ErrorCategory.JSErrorList}>JS错误列表</Radio.Button>
                <Radio.Button value={ErrorCategory.PageList}>页面列表</Radio.Button>
                <Radio.Button value={ErrorCategory.ErrorInfoList}>错误信息列表</Radio.Button>
            </Radio.Group>
            {category === ErrorCategory.JSErrorList && <JSErrorList onClick={showDrawer}></JSErrorList>}
            {category === ErrorCategory.PageList && <PageList></PageList>}
            {category === ErrorCategory.ErrorInfoList && <ErrorInfoList></ErrorInfoList>}
        </Card>
        <Drawer
            title="错误信息标题"
            placement='right'
            closable={false}
            width='80%'
            onClose={onClose}
            open={open}
            extra={
                <Space>
                    <CloseOutlined onClick={onClose} />
                </Space>
            }
        >
            <p>Some contents...</p>
            <p>Some contents...</p>
            <p>Some contents...</p>
        </Drawer>
    </div>);
}

export default View;